[data-type="dining-dashboard/menu-section"] .editor-block-list__layout {
  display: flex;
  flex-wrap: wrap; }

[data-type="dining-dashboard/menu-section-heading"] {
  width: 100%; }

[data-type="dining-dashboard/menu-section-column"] {
  margin: 0; }
  .one-col [data-type="dining-dashboard/menu-section-column"] {
    width: 100%; }
  .two-cols [data-type="dining-dashboard/menu-section-column"] {
    width: 50%; }
  .three-cols [data-type="dining-dashboard/menu-section-column"] {
    width: 33.3333%; }
  .four-cols [data-type="dining-dashboard/menu-section-column"] {
    width: 25%; }
  [data-type="dining-dashboard/menu-section-column"] .block-list-appender {
    width: 96%;
    margin: 0 auto; }
