[data-type="dining-dashboard/menu-section"] {
    
    .editor-block-list__layout {
        display: flex;
        flex-wrap: wrap;
    }
}

[data-type="dining-dashboard/menu-section-heading"] {
    width: 100%;
}



[data-type="dining-dashboard/menu-section-column"] {
    margin: 0;

    .one-col & {
        width: 100%;
    }

    .two-cols & {
        width: 50%;
    }

    .three-cols & {
        width: 33.3333%;
    }

    .four-cols & {
        width: 25%;
    }

    .block-list-appender {
        width: 96%;
        margin: 0 auto;
    }
}